body {
  font-family: Gill Sans, sans-serif;
  background-color: #C768FF;
  margin: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

.container {
  text-align: center;
  padding: 20px;
  background-image: linear-gradient(rgba(4, 0, 0, 0.71), rgba(21, 21, 1, 0.5));
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  border-radius: 10px;
  width: 90%;
  max-width: 600px;
  /* Adjust for larger screens */
  overflow-y: auto;
  /* Enable vertical scrolling */
  max-height: 80vh;
  /* Limit max height */
  margin: 20px;
  color: #ede5e5;
  text-shadow: 0.5px 0.2px #ffffff;
}

.input,
.color-picker,
.select {
  padding: 10px;
  margin: 10px 0;
  width: calc(100% - 24px);
  font-size: 16px;
  color: #000000;
  background-color: #dcd1d1fb;
  border-radius: 2%;
}

.button,
.download-button {
  padding: 10px 20px;
  margin: 10px 0;
  width: 100%;
  font-size: 16px;
  cursor: pointer;
  background-color: #c869ff8b;
  color: white;
  border: none;
  border-radius: 5px;
  transition: background-color 0.3s, transform 0.3s;
}

.button:hover,
.download-button:hover {
  background-color: #c668fc;
  transform: scale(1.05);
}

.button:active,
.download-button:active {
  transform: scale(0.95);
}

.options {
  text-align: left;
  margin-top: 20px;
}

.label {
  display: block;
  margin-bottom: 10px;
}

.qr-container {
  margin-top: 20px;
}

.qr-wrapper {
  position: relative;
  display: inline-block;
  text-align: center;
}

.qr-gradient-overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(60deg, #e7eaec, #000500, #ff0000);
  opacity: 0.3;
  pointer-events: none;
  border-radius: inherit;
}

.qr-logo {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  max-width: 30%;
  /* Adjust size */
  max-height: 30%;
  /* Adjust size */
  pointer-events: none;
  z-index: 1;
}

@media (max-width: 768px) {
  .container {
    padding: 15px;
  }
}

@media (max-width: 600px) {
  .container {
    padding: 10px;
    max-width: 100%;
    /* Adjust to full width */
  }
}

@media (max-width: 480px) {

  .input,
  .color-picker,
  .select {
    width: calc(100% - 20px);
    padding: 8px;
    font-size: 14px;
  }

  .button,
  .download-button {
    padding: 8px 16px;
    font-size: 14px;
  }

  .qr-logo {
    max-width: 50%;
    /* Adjust size */
    max-height: 50%;
    /* Adjust size */
  }
}
.footer {
  text-align: center;
  margin-top: 20px;
  font-size: 14px;
  background-color: #e7eaec;
  border-radius: 0%;
  padding: 5px;
}

.footer a {
  color: #1084ff;
  text-decoration: none;
}

.footer a:hover {
  text-decoration: underline;
}